import React, { Component } from "react";
import "./App.css";
import champions from "./champion.json";
import Header from "./utility/Header";
import Modal from "./utility/Modal";
import Login from "./components/login/Login";
import InfoPageItem from "./components/login/InfoPageItem";
import SummonersSelector from "./components/login/SummonersSelector";

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      roles: ["Top", "Jungle", "Mid", "Bottom", "Support"],
      randomChampions: [],
      showModal: "",
	  summonerName: "",
	  releases:[]
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.onChange = this.onChange.bind(this);
    this.saveSummonerName = this.saveSummonerName.bind(this);
    this.logout = this.logout.bind(this);
  }

  componentDidMount() {
    let summonerName = localStorage.getItem("summonerName");
    this.setState({
      randomChampions: this.rollChampions(5),
      summonerName: summonerName !== "undefined" ? summonerName : ""
    });
  }
  toggleModal(modal) {
    this.setState({
      showModal: modal
    });
  }
  onChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }
  saveSummonerName(e) {
    e.preventDefault();
    if (/^[0-9a-z _.]+$/.test(this.state.summonerName)) {
      localStorage.setItem("summonerName", this.state.summonerName);
      this.toggleModal("");
      this.setState({ msg: "" });
    } else {
      this.setState({
        msg: "Invalid summoner name"
      });
    }
  }
  logout() {
    console.log("asdsa");
    localStorage.setItem("summonerName", "");
    this.setState({
      summonerName: ""
    });
  }
  onClick = (name) => this.setState({[name]:!this.state[name]})

  onChangesClick = async () => {
	if(this.state.releases.length === 0){
		let res = await fetch(
			"https://api.github.com/repos/spoof14/araf/releases"
		);
		res = await res.json();
		this.setState({ releases: res });
	}
	this.setState({seeChanges:!this.state.seeChanges})
  }

  render() {
    let { randomChampions, showModal, summonerName, msg } = this.state;
    let divs = randomChampions.map((champ, index) => {
      return (
        <div
          key={champ.name}
          className="champion-list-item"
          onClick={() => this.rerollChampion(index)}
        >
          <span>{champ.name}</span>
          <img
            src={require(`./championImages/${champ.image}`)}
            height="100px"
            width="100px"
            alt="champion"
          ></img>
          <span>{this.state.roles[index]}</span>
        </div>
      );
    });
    //				<Header title="All Random All Fill" onClick={() => !!summonerName ? this.logout() : this.toggleModal('login')} hasToken={!!summonerName}></Header>

    return (
      <div className="App">
        <div className="champion-list">{divs}</div>

        <footer
          className="footer"
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            width: "80%",
            margin: "0 10% 2rem 10%"
          }}
        >
          <div onClick={this.onChangesClick}>
            {this.state.seeChanges
              ? "Click me to use the app"
              : "Click me to see changes"}
          </div>
          <div onClick={() => this.onClick("chooseSummoners")}>
            Choose summoners
          </div>
        </footer>
        {showModal === "login" && (
          <Modal showModal={showModal}>
            <Login
              onChange={this.onChange}
              onSubmit={this.saveSummonerName}
              msg={msg}
            ></Login>
          </Modal>
        )}
        {this.state.seeChanges && (
          <Modal
            showModal={this.state.seeChanges}
            onClick={() => this.onClick('seeChanges')}
          >
            {this.state.releases.map(r => (
              <InfoPageItem key={r.id} props={r}></InfoPageItem>
            ))}
          </Modal>
        )}
        {this.state.chooseSummoners && <Modal onClick={() => this.onClick('chooseSummoners')} showModal={this.state.chooseSummoners}>
			<SummonersSelector/>
		</Modal>}
      </div>
    );
  }

  rollChampions() {
    let champs = [];
    while (champs.length < 5) {
      let element = this.rollChampion();

      if (!this.someChampIsSame(champs, element)) champs.push(element);
    }
    return champs;
  }

  rollChampion() {
    var random = Math.floor(Math.random() * Object.keys(champions.data).length);
    var key = Object.keys(champions.data)[random];
    var element = champions.data[key];
    return { name: element.name, image: element.image.full };
  }

  someChampIsSame(array, newChamp) {
    return array.some(champ => champ.name === newChamp.name);
  }

  rerollChampion(index) {
    let { randomChampions } = this.state;
    let currChamp = randomChampions[index];
    let newChamp = currChamp;

    while (
      newChamp === currChamp ||
      this.someChampIsSame(randomChampions, newChamp)
    ) {
      newChamp = this.rollChampion();
    }
    randomChampions[index] = newChamp;
    this.setState({
      randomChampions
    });
  }
}

export default App;
